$env: 'production';
@import '~styles/base';

.controls {
  display: flex;
  align-items: center;
  margin: 6px -6px;
}

.controlButtonIcon {
  position: relative;
  margin-left: 5px;
  display: flex;
  align-items: center;
  overflow: visible;

  svg {
    width: 24px;
    fill: $maincolor-lt;
    transition: fill .15s ease-in-out;
  }
}

.timer {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 34px;
  background: $maincolor-lt;
  border-radius: 15px;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .15s ease-in-out;
  transform: translate(-1px, y);
  margin-right: 10px;
  font-size: 13px;

  &.hide {
    display: none;
  }
}

.buttonStartInfo {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 34px;
  height: 34px;
  margin-left: -20px;
}


.controlButton {
  margin: 0 6px;
  display: flex;

  &:hover,
  &:active {
    .controlButtonIcon svg {
      fill: $maincolor-bd;
    }

    .timer {
      background: $maincolor-bd;
    }
  }
}

.audioWrapper {
  display: flex;
  align-items: center;
}

.audioRemove {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 34px;
  cursor: pointer;
  color: $redPale;
  margin-left: 12px;
  opacity: .8;
  transition: color .15s ease-in-out, opacity .15s ease-in-out;

  svg {
    width: 14px;
    fill: $redPale;
    transition: fill .15s ease-in-out;
    margin-right: 2px;
  }

  &:hover {
    color: $redPaleHover;
    opacity: 1;

    svg {
      fill: $redPaleHover;
    }
  }
}



