$env: 'production';
@import '~styles/base';

.modal {
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  outline: none;
  border-radius: 4px;
  background: $white;
  width: 420px;
  max-height: 100vh;
  //overflow-y: auto;
  text-align: center;

  &.big {
    width: 800px;
  }
}

.modalClose {
  position: absolute;
  top: 32px;
  right: 28px;
  cursor: pointer;

  svg {
    width: 12px;
    height: 12px;
    fill: $noncolor-t13;
    transition: fill .1s ease-in-out;
  }

  &:hover {
    opacity: 1;

    svg {
      fill: $blue;
    }
  }
}

.modalBody {
  border-radius: 4px;
}



