$env: 'production';
@import '~styles/base';

.wrapper {
  position: relative;
}

.label {
  padding: 2px 0 7px !important;
  outline: none;
  display: block;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.2;
  color: $noncolor-t3;
  cursor: pointer;

  // Size
  &.large {
    padding-bottom: 7px !important;
  }
}

.input {
  display: block;
  width: 100%;
  height: 42px;
  padding: 0 18px;
  margin: 0 0 0 0;
  box-sizing: border-box;
  outline: 0;
  background-color: $white;
  border: 1px solid $grey-border;
  border-radius: 40px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  color: $mainText;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  -webkit-appearance: none;

  &::placeholder {
    color: $placeholderText;
  }

  &:hover {
    border-color: $grey-border-hover;
  }

  &:focus {
    border-color: $maincolor-lt;
  }

  &.invalid {
    border-color: $error;
  }

  // Size
  &.large {
    height: 50px;
  }

  &.small {
    height: 32px;
  }

  &[disabled] {
    background: $grey-border;

    &:hover,
    &:focus,
    &:active {
      background: $grey-border;
      border-color: $grey-border;
    }
  }
}

.error {
  bottom: -15px;
  right: 26px;

  &.top {
    top: 1px;
    bottom: auto;
  }

  &.topForSmall {
    top: -20px;
    bottom: auto;
  }
}

.statusMessage {
  position: absolute;
  right: 2px;
  bottom: -22px;
  height: 16px;
  color: $color-black-nr;

  &.top {
    top: 1px;
    bottom: auto;
  }

  &.topForSmall {
    top: -19px;
  }
}

.statusIconsWrapper {
  position: absolute;
  right: 8px;
  top: 0;

  &.topForSmall {
    top: 10px;
  }
}

.statusIconSuccess {
  svg {
    fill: $noncolor-t25;
  }
}

.statusIconError {
  svg {
    fill: $errorMessage;
  }
}



