$env: 'production';
@import '~styles/base';

.sideImage {
  display: flex;
  background-size: cover;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 820px;
}
.success {
  color: $success;
  padding: 5px 10px;
  font-size: 13px;
}
.error {
  color: $error;
  padding: 5px 10px;
  font-size: 13px;
}
@media (max-width: 1100px) {
  .sideImage {
    display: none !important;
  }
}



