$env: 'production';
@import '~styles/base';

.wrapper {
  padding: 12px 30px;
}

.note {
  color: $greyText;
  font-size: 13px;
  display: flex;
  align-items: center;
  margin: -12px 0 24px;
}

.noteIcon {
  margin-right: 4px;

  svg {
    fill: $greyText;
    width: 20px;
  }
}



